import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import { Link } from "gatsby";
import TalkToUsNew from "../../components/common/talk-to-us-new";
import CtaButton from "../../components/common/contactbutton";
import Faq from "../../components/common/faq";
import ContactButton from "../../components/common/connect-button";

import hero_img from "../../styles/assets/images/poc/poc_hero.webp";
import hero_blob from "../../styles/assets/images/poc/hero_blob.webp";
import proof_img from "../../styles/assets/images/poc/poc_proof.webp";
import proof_blob from "../../styles/assets/images/poc/proof_blob.webp";
import poc_purpose from "../../styles/assets/images/poc/poc_purpose.webp";

import objective from "../../styles/assets/images/poc/objective.png";
import prototype from "../../styles/assets/images/poc/prototype.png";
import mvp from "../../styles/assets/images/poc/mvp.png";
import { utils } from "../../core/helper";
import { StaticImage } from "gatsby-plugin-image";
import CtaTwo from "../../components/common/contactbuttontwo";
import CtaBtnThree from "../../components/common/contactbutthonthree";

const DevelopmentServices = (props) => {
  const { db } = props;

  const pocFaqData = [
    {
      question: "What is the Proof of Concept in Software Development?",
      answer:
        "A demonstration of a concept's feasibility, like a product idea or a business plan.",
    },
    {
      question: "What is blockchain proof of concept?",
      answer:
        "It utilizes mining device’s hard drive space to determine mining rights and validate transactions.",
    },
    {
      question: "What is blockchain proof of work?",
      answer: "It is a consensus algorithm for a blockchain network.",
    },
  ];

  return (
    <div className="poc_development">
      <div className="poc_hero_bg">
        <Container>
          <Row>
            <Col lg={6}>
              <div className="poc_hero_lhs">
                <h1 className="poc_hero_title">
                  Proof-of-Concept (POC) Development Company
                </h1>
                <p className="poc_hero_text">
                  With a team of skilled Proof-of-Concept developers, we
                  demonstrate a product's technical feasibility and market
                  potential by leveraging our state-of-the-art POC development
                  services.
                </p>
                <div className="decenter_hero_sec_btn">
                  <Link to="/contact-us" className="demo_btn">
                    View Live Demo
                  </Link>
                  <button type="submit" className="consult_btn">
                    <TalkToUsNew label="Free Consult" db={db} />
                  </button>
                </div>
              </div>
            </Col>
            <Col lg={6} className="col_flex">
              <div className="poc_hero_rhs">
                <img
                  className="poc_image"
                  src={hero_img}
                  alt="poc open source development"
                />
                <img className="hero_blob" src={hero_blob} alt="blob" />
              </div>
            </Col>
          </Row>
        </Container>
      </div>
      <Container>
        <div className=" poc_def_wrapper">
          <Row className="gy-5">
            <Col lg={6}>
              <div className="poc_def_rhs">
                <img
                  className="poc_proof_img"
                  src={proof_img}
                  alt="poc open source development"
                />
                <img className="poc_proof_blob" src={proof_blob} alt="blob" />
              </div>
            </Col>
            <Col lg={6}>
              <div className="poc_def_lhs">
                <h1>What is Proof of Concept (POC)?</h1>
                <p>
                  A Proof of Concept(POC), serves as a prototype necessary to
                  evaluate the potential of blockchain technology. It provides
                  clear evidence of the success or failure of a product. POC
                  software development companies use this concept to address
                  gaps that may hinder the success of a blockchain concept.
                </p>
                <p>
                  If you are looking to buy a product, you can trust the POC as
                  it will give you insider feedback and help you understand the
                  risks associated with a blockchain product.
                </p>
                <ContactButton justifyContentPosition="connect_start" />
              </div>
            </Col>
          </Row>
        </div>
      </Container>
      <div className="poc_proof_bg">
        <Container>
          <Row className="poc_proof_row">
            <Col md={12} className="col_flex mb-5">
              <h1 className="text-center  text-white">
                Purpose of Proof of Concept (POC)?
              </h1>
            </Col>

            <Col lg={6} className="col_flex">
              <div className="poc_proof_lhs">
                <p className="poc_proof_text">
                  At Justtry Tech, Bring your ideas to life by preparing a proof
                  of concept (POC) for a blockchain project. Our expert team
                  analyzes feasibility, providing a comprehensive POC.
                  Post-development, gain a clear understanding of conceptual
                  projects, with or without codes. Witness your idea evolve into
                  a real-time project through our blockchain-based Proof of
                  Concept development.
                </p>
                <div className="decenter_hero_sec_btn">
                  <Link to="/contact-us" className="demo_btn">
                    View Live Demo
                  </Link>
                  <button type="submit" className="consult_btn">
                    <TalkToUsNew label="Free Consult" db={db} />
                  </button>
                </div>
              </div>
            </Col>
            <Col lg={6} className="col_flex">
              <StaticImage
                className="poc_proof_rhs"
                imgClassName="poc_proof_image"
                src="../../styles/assets/images/poc/poc.webp"
                alt="proof of concept"
              />
            </Col>
          </Row>
        </Container>
      </div>

      <Container>
        <div className="poc_approach_wrapper">
          <Row>
            <h1 className="text-center">
              Our Approach to Proof of Concepts (POC)
            </h1>
          </Row>
          <Row className="poc_approach_row">
            <Col lg={4} className="col_flex">
              <div className="poc_approach_card">
                <div className="poc_approach_box">
                  <img
                    className="poc_objective"
                    src={objective}
                    alt="poc objective"
                  />
                  <div className="poc_approach_title">Project Objective</div>
                </div>

                <div className="poc_approach_body">
                  A theoretical plan is crucial for any project, providing a
                  list of tasks, expected outcomes, pros, and cons. Proposals
                  are created to explain the project to stakeholders, leading to
                  further discussions.
                </div>
              </div>
            </Col>
            <Col lg={4} className="col_flex">
              <div className="poc_approach_card">
                <div className="poc_approach_box">
                  <img
                    className="poc_prototype"
                    src={prototype}
                    alt="POC Prototype"
                  />
                  <div className="poc_approach_title">Prototype Building</div>
                </div>

                <div className="poc_approach_body">
                  After stakeholder discussions, the next step is creating a
                  prototype with working architecture, sketches, codes, design,
                  and tested products.
                </div>
              </div>
            </Col>
            <Col lg={4} className="col_flex">
              <div className="poc_approach_card">
                <div className="poc_approach_box">
                  <img className="poc_mvp" src={mvp} alt="mvp poc" />
                  <div className="poc_approach_title">MVP</div>
                </div>

                <div className="poc_approach_body">
                  Basically, the goal is to create a perfect product with
                  outstanding architecture and minimal features. This phase
                  includes the ability for end users to test the product for the
                  project.
                </div>
              </div>
            </Col>
            <Col md={12}>
              <ContactButton justifyContentPosition="connect_center" />
            </Col>
          </Row>
        </div>
      </Container>
      <CtaBtnThree db={db} />
      <Container>
        <div className="poc_innovation_wrapper">
          <Row>
            <h1 className="text-center">
              Proof-of-Stack (POC) in specific domains
            </h1>
          </Row>
          <Row className="poc_innovative_row">
            <Col lg={4} md={6} col={12} className="col_flex">
              <div className="poc_innovative_card">
                <div className="poc_innovative_card_header">
                  <div className="poc_innovative_card_svg">
                    {utils.blockchain()}
                  </div>
                  Blockchain-Based
                  <br /> Voting System
                </div>
                <div className="poc_innovative_card_body">
                  A proof of concept in open source development for a
                  blockchain-based voting system demonstrates the feasibility of
                  a transparent, secure, and decentralized electoral process,
                  ensuring the integrity of democratic practices.
                </div>
              </div>
            </Col>

            <Col lg={4} md={6} col={12} className="col_flex">
              <div className="poc_innovative_card">
                <div className="poc_innovative_card_header">
                  <div className="poc_innovative_card_svg">{utils.iam()}</div>
                  Identity Management
                </div>
                <div className="poc_innovative_card_body">
                  Through an open source proof of concept, blockchain identity
                  management solutions showcase their potential to empower
                  individuals with control over their digital identities,
                  fostering privacy and security in a transparent manner.
                </div>
              </div>
            </Col>

            <Col lg={4} md={6} col={12} className="col_flex">
              <div className="poc_innovative_card">
                <div className="poc_innovative_card_header">
                  <div className="poc_innovative_card_svg">
                    {utils.asset_web3()}
                  </div>
                  Asset Management
                </div>
                <div className="poc_innovative_card_body">
                  An open source proof of concept for blockchain asset
                  management validates the efficiency gains in tracking and
                  managing assets across supply chains, providing a
                  decentralized, tamper-resistant ledger for improved
                  transparency and accountability.
                </div>
              </div>
            </Col>
            <Col lg={4} md={6} col={12} className="col_flex">
              <div className="poc_innovative_card">
                <div className="poc_innovative_card_header">
                  <div className="poc_innovative_card_svg">
                    {utils.bank_web3()}
                  </div>
                  Banking And Finance
                </div>
                <div className="poc_innovative_card_body">
                  In open source development, a proof of concept for blockchain
                  in banking and finance highlights the potential for
                  cost-effective, transparent, and secure transactions,
                  demonstrating the advantages of decentralized ledgers and
                  smart contracts.
                </div>
              </div>
            </Col>
            <Col lg={4} md={6} col={12} className="col_flex">
              <div className="poc_innovative_card">
                <div className="poc_innovative_card_header">
                  <div className="poc_innovative_card_svg">
                    {utils.healthcare_web3()}
                  </div>
                  Medical And Healthcare
                </div>
                <div className="poc_innovative_card_body">
                  An open source proof of concept for blockchain in healthcare
                  emphasizes the interoperability and security of patient data,
                  showcasing the potential for decentralized and transparent
                  medical record management to drive innovation in the industry.
                </div>
              </div>
            </Col>
            <Col lg={4} md={6} col={12} className="col_flex">
              <div className="poc_innovative_card">
                <div className="poc_innovative_card_header">
                  <div className="poc_innovative_card_svg">
                    {utils.retail()}
                  </div>
                  Retail And Manufacturing
                </div>
                <div className="poc_innovative_card_body">
                  Through open source development, a proof of concept for
                  blockchain in retail and manufacturing establishes the
                  viability of enhancing supply chain efficiency, reducing
                  counterfeiting, and ensuring transparency from production
                  toconsumer, utilizing a decentralized ledger system.
                </div>
              </div>
            </Col>
          </Row>
        </div>
      </Container>
      <CtaTwo db={db} />

      <div className="poc_choose_wrapper">
        <Container>
          <Row className="poc_choose_row">
            <Col lg={5} className="col_flex">
              <div className="poc_choose_lhs">
                <span className="poc_choose_text">
                  Why Choose <br />
                  Justtry Tech <br />
                  Your Premier <br />
                  POC Development Company?
                </span>
                <ContactButton justifyContentPosition="connect_start" />
              </div>
            </Col>
            <Col lg={7} className="col_flex">
              <Row className="gy-2">
                <Col md={6} col={12} className="col_flex">
                  <div className="poc_choose_rhs_card">
                    <p className="poc_choose_title">
                      Expertise in POC Development
                    </p>
                    <p className="poc_choose_text">
                      At Justtry Tech, we bring unparalleled expertise to the
                      table as your premier POC development company. Our
                      seasoned professionals specialize in crafting proof of
                      concepts that showcase the feasibility and potential of
                      your innovative ideas.
                    </p>
                  </div>
                </Col>
                <Col md={6} col={12} className="col_flex">
                  <div className="poc_choose_rhs_card">
                    <p className="poc_choose_title">
                      Cutting-Edge Tech Solutions
                    </p>
                    <p className="poc_choose_text">
                      Choose us for POC development that leverages cutting-edge
                      technologies. Our team integrates the latest advancements
                      to create tech prototypes that align with industry trends,
                      ensuring your concepts are at the forefront of innovation.
                    </p>
                  </div>
                </Col>
                <Col md={6} col={12} className="col_flex">
                  <div className="poc_choose_rhs_card">
                    <p className="poc_choose_title">
                      Precision and Scalability
                    </p>
                    <p className="poc_choose_text">
                      We prioritize precision in POC development, ensuring every
                      aspect aligns with your vision. Additionally, scalability
                      is ingrained in our process, allowing your concepts to
                      evolve seamlessly from initial proof of concept to
                      full-scale implementation.
                    </p>
                  </div>
                </Col>
                <Col md={6} col={12} className="col_flex">
                  <div className="poc_choose_rhs_card">
                    <p className="poc_choose_title">
                      Customer-Centric Approach
                    </p>
                    <p className="poc_choose_text">
                      At Justtry Tech, we adopt a customer-centric approach in
                      POC development. We understand your unique needs and
                      aspirations, tailoring our services to provide a
                      personalized experience. Partner with us for POC
                      development that exceeds expectations.
                    </p>
                  </div>
                </Col>
              </Row>
            </Col>
          </Row>
        </Container>
      </div>
      <Faq faqData={pocFaqData} />
    </div>
  );
};

export default DevelopmentServices;
