import Layout from "../components/layout";
import React from "react";
import PocOpenSourceScreen from "../screens/poc-open-source";
import Seo from "../components/seo";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";
import MoveForward from "../components/common/move-forward";
import BreadCrumb from "../components/common/breadcrumb";

const PocOpenSource = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "POC and Open Source Development Services", link: null },
  ];

  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <PocOpenSourceScreen db={db} />
      <MoveForward db={db} />
    </Layout>
  );
};

export const Head = () => (
  <Seo
    title="POC Open-Source Development Company | Justtry Technologies"
    headTitle="POC Development Company | POC And Open Source Development Services"
    description="Justtry Tech is the leading POC Development Company, specializing in Blockchain Proof of Concept (POC) software development services. It empowers you to build prototypes for your projects with ease."
    keywords="POC, proof of concept, poc development services, proof of concept development services, POC for blockchain application, Proof of Concept (POC) Development, Open-Source POC Solutions, POC Software Development, Prototype Development Services, , POC Application Design"
  ></Seo>
);

export default PocOpenSource;

export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
